:root {
  --background: #0a0a0a;
  --foreground: #ededed;
  --gold: #C5AE79;
  --gold-dim: rgba(197, 174, 121, 0.3);
  --card: #111111;
  --card-border: rgba(197, 174, 121, 0.2);
  --muted: #a1a1aa;
  --destructive: #ff0055;
  --cyan: #00ffff;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
  overflow: hidden;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: var(--gold-dim);
  border-radius: 3px;
}
input:focus {
  outline: none;
  border-color: var(--gold);
  box-shadow: 0 0 0 2px rgba(197, 174, 121, 0.2);
}
button:focus-visible {
  outline: 2px solid var(--gold);
  outline-offset: 2px;
}
